import React from 'react';
import ReactDOM from 'react-dom';
import SVGContainer from './svgContainer.js';
import BPObject from './BPObject.js';
import 'core-js/fn/array/find';

export default class BPObjectsContainer extends React.Component {

  constructor(){
    super();
    this.onMount = this.onMount.bind(this);
    this.state = {components: [], lines: [], schemeRight: 0, schemeBottom: 0};
    this.lines = true;
    this.isIE = function(){
      return navigator.userAgent.match(/Edge/i) || navigator.userAgent.match(/Trident.*rv[ :]*11\./i) != null;
    }
  }

  onMount(component,componentRight,componentBottom) {
    let components = this.state.components;
    components.push(component);
    this.setState({
      components: components,
      schemeRight: Math.max(componentRight,this.state.schemeRight),
      schemeBottom: Math.max(componentBottom,this.state.schemeBottom)
    });
  }

  drawLines(){
    const lines = this.props.relations.map(function(relation){
      const startObject = this.state.components.find((component) => component.props.id === relation.start_object);
      const endObject = this.state.components.find((component) => component.props.id === relation.end_object);
      if (startObject && endObject){
        const startObjectNodeRect = ReactDOM.findDOMNode(startObject).getBoundingClientRect();
        const endObjectNodeRect = ReactDOM.findDOMNode(endObject).getBoundingClientRect();
        let x1 = 1, x2 = 1, y1 = 1, y2 = 1;
        const left = startObject.props.left > endObject.props.left + endObjectNodeRect.width;
        const right = startObject.props.left + startObjectNodeRect.width < endObject.props.left;
        const top = startObject.props.top > endObject.props.top + endObjectNodeRect.height;
        const bottom = startObject.props.top + startObjectNodeRect.height < endObject.props.top;
        const startObjectBottom = startObject.props.top + startObjectNodeRect.height;
        const startObjectMiddleHeight = startObjectBottom - startObjectNodeRect.height/2;
        const startObjectMiddleWidth = startObject.props.left + startObjectNodeRect.width/2;
        const endObjectBottom = endObject.props.top + endObjectNodeRect.height; 
        const endObjectMiddleHeight = endObjectBottom - endObjectNodeRect.height/2;
        const endObjectMiddleWidth = endObject.props.left + endObjectNodeRect.width/2;
        if (left){
          if (top){
            x1 = startObjectMiddleWidth;
            y1 = startObject.props.top;
            y2 = endObjectMiddleHeight;
          }
          else if (bottom){
            x1 = startObjectMiddleWidth;
            y1 = startObjectBottom;
            y2 = endObjectMiddleHeight;
          }
          else {
            x1 = startObject.props.left;
              if (startObject.props.type === 'routing' && startObjectMiddleHeight >= endObject.props.top && startObjectMiddleHeight <= endObjectBottom){
                y1 = startObjectMiddleHeight;
                y2 = y1;
              }
              else if (endObject.props.type === 'routing' && endObjectMiddleHeight >= startObject.props.top && endObjectMiddleHeight <= startObjectBottom){
                y2 = endObjectMiddleHeight;
                y1 = y2;
              }
            else {
              const intersectTop = Math.max(startObject.props.top, endObject.props.top);
              const intersectBottom = Math.min(startObject.props.top + startObjectNodeRect.height, endObject.props.top + endObjectNodeRect.height);
              y1 = (intersectBottom + intersectTop)/2;
              y2 = y1;
            }
          }
          x2 = endObject.props.left + endObjectNodeRect.width;
        }
        else if (right){
          if (top){
            x1 = startObject.props.left + startObjectNodeRect.width/2;
            y1 = startObject.props.top;
            y2 = endObject.props.top + endObjectNodeRect.height/2;
          }
          else if (bottom){
            x1 = startObject.props.left + startObjectNodeRect.width/2;
            y1 = startObject.props.top + startObjectNodeRect.height;
            y2 = endObject.props.top + endObjectNodeRect.height/2;
          }
          else {
            x1 = startObject.props.left + startObjectNodeRect.width;
            if (startObject.props.type === 'routing' || endObject.props.type === 'routing'){
              if (startObject.props.type === 'routing' && startObjectMiddleHeight >= endObject.props.top && startObjectMiddleHeight <= endObjectBottom){
                y1 = startObjectMiddleHeight;
                y2 = y1;
              }
              else if (endObject.props.type === 'routing' && endObjectMiddleHeight >= startObject.props.top && endObjectMiddleHeight <= startObjectBottom){
                y2 = endObjectMiddleHeight;
                y1 = y2;
              }
              else {
                y1 = startObjectMiddleHeight;
                y2 = endObjectMiddleHeight;
              }
            }
            else {
              const intersectTop = Math.max(startObject.props.top, endObject.props.top);
              const intersectBottom = Math.min(startObjectBottom, endObjectBottom);
              y1 = (intersectBottom + intersectTop)/2;
              y2 = y1;
            }
          }
          x2 = endObject.props.left;
        }
        else {
          x1 = startObjectMiddleWidth;
          x2 = endObjectMiddleWidth;
          if(top) {
            y1 = startObject.props.top;
            y2 = endObjectBottom;
          }
          else if (bottom){
            y1 = startObjectBottom;
            y2 = endObject.props.top;
          }
        }
        x2 -= SVGContainer.MARKERSIZE*(x2-x1)/Math.sqrt((x2-x1) ** 2 + (y2-y1) ** 2);
        y2 -= SVGContainer.MARKERSIZE*(y2-y1)/Math.sqrt((x2-x1) ** 2 + (y2-y1) ** 2);
        return {
          x1: x1,
          x2: x2,
          y1: y1,
          y2: y2,
          label: relation.label
        };
      }
    }.bind(this));
    this.setState({lines: lines})
  }

  componentDidMount() {
    this.drawLines();
  }

  componentWillReceiveProps(nextProps){
    if(nextProps.passStatuses !== this.props.passStatuses){
      this.needRedrawLines = true;
      this.needExtendContainer = true;
    }
  }

  componentDidUpdate(){
    if (this.needRedrawLines){
      this.needRedrawLines = false;
      if(this.isIE()){
        this.IELinesReconfigure = true;
      }
      this.drawLines();
    }
    if (this.needExtendContainer){
      this.needExtendContainer = false;
      const newBottomReducer = (currentBottom,component) => Math.max(currentBottom,component.props.top + ReactDOM.findDOMNode(component).offsetHeight)
      const newBottom = this.state.components.reduce(newBottomReducer,this.state.schemeBottom);
      if (this.state.schemeBottom <newBottom){
        this.setState({schemeBottom: newBottom});
      }
    }
    if (this.isIE() && this.IELinesReconfigure){
      this.IELinesReconfigure = false;
      [].forEach.call(document.querySelectorAll('line'), function(line){
        const parentNode = line.parentNode;
        parentNode.removeChild(line);
        parentNode.insertBefore(line, parentNode.firstChild);
      });
    }
  }

  render(){
    this.bpObjects = this.props.bpObjects.map(function(config){
        const passStatus = this.props.passStatuses.find((passStatus) => passStatus.id === config.id);
        return <BPObject key={config.id} id={config.id} gumcid={this.props.gumcid} code={config.code} left={config.left} top={config.top} type={config.type} name={config.name} onMount={this.onMount} onUpdate={this.onUpdate} passStatus={passStatus} excludedTypes={this.props.excludedTypes}/>
      }.bind(this)
    );
    const containerStyle = {width: this.state.schemeRight+'px', height: this.state.schemeBottom + 'px'};
    return (
      <div style={containerStyle} className="bp-objects-container">
        {this.bpObjects}
        <SVGContainer width={this.state.schemeRight} height={this.state.schemeBottom} lines={this.state.lines}/>
      </div>
    );
  }

}


