import React from 'react';
import BPObjectsContainer from './BPObjectsContainer.js';
import GUMCIDInput from './gumcidInput.js';
import Header from './header.js';

export default class BPM extends React.Component {

  constructor(){
    super();
    this.onResponse = this.onResponse.bind(this);
    this.state = {passStatuses: [], gumcid: null};
  }

  onResponse(responseText, gumcid){

    if(responseText.length){
      const response = JSON.parse(responseText);

      if(response.success && Array.isArray(response.result)){
        this.setState({passStatuses: response.result, gumcid: gumcid})
      }
    }
  }
  
  render() {
    return (
      <div>
        <Header gumcid={this.state.gumcid} title={this.props.title}/>
        <GUMCIDInput bpObjects={this.props.bpObjects} onResponse={this.onResponse} loadUrl={this.props.config.url} excludedTypes = {this.props.config.excludedTypes}/>
        <BPObjectsContainer gumcid={this.state.gumcid} passStatuses={this.state.passStatuses} bpObjects={this.props.bpObjects} relations={this.props.relations} excludedTypes = {this.props.config.excludedTypes}/>
      </div>
    );
  }
}


