import React from 'react';
import ReactDOM from 'react-dom';
import Relation from './relation.js';

export default class SVGContainer extends React.Component {

  static get MARKERSIZE() {
    return 10;
  }

  componentDidMount() {
    const node = ReactDOM.findDOMNode(this);
    this.setState({top: node.getBoundingClientRect().top});
  }

  render() {
    const relationComponents = this.props.lines.map((lineConfig) =>
      <Relation key={lineConfig.label} lineConfig={lineConfig} top={this.state.top}/>
    );
    return (
      <svg className="svg-container">
        <defs>
          <marker id="arrow" markerWidth={SVGContainer.MARKERSIZE} markerHeight={SVGContainer.MARKERSIZE} refX="0" refY="3" orient="auto" markerUnits="strokeWidth">
            <path d="M0,0 L0,6 L9,3 z" fill="black"></path>
          </marker>
        </defs>
        {relationComponents}
      </svg>
    );
  }
}


