import React from 'react';

export default class Header extends React.Component {
  
  render(){

    const gumcidText = this.props.gumcid ? ` для сообщения ${this.props.gumcid}` : '';

    return (
      <h3 className="bpm-header">
        {this.props.title}{gumcidText}
      </h3>
    );
      
  }

}


