import React from 'react';

export default class Relation extends React.Component {

  static get OFFSET() {
    return 4;
  }

  constructor(){
    super();
    this.state = {rectCoords: {x: 0, y: 0, width: 0, height: 0}};
  }

  componentWillReceiveProps(nextProps){
    this.needRedrawRect = true;
  }

  componentDidUpdate(){
    if(this.needRedrawRect){
      this.needRedrawRect = false;
      this.drawRect();
    }
  }

  componentDidMount(){
    this.drawRect();
  }


  drawRect(){
    const text = this.refs.text;
    const textRect = text.getBoundingClientRect();
    const lineConfig = this.props.lineConfig;
    const rectCoords ={
      x: (lineConfig.x1 + lineConfig.x2)/2 - textRect.width/2 - Relation.OFFSET,
      y: (lineConfig.y1 + lineConfig.y2)/2 - textRect.height/2 - Relation.OFFSET,
      width: textRect.width + Relation.OFFSET*2,
      height: textRect.height + Relation.OFFSET*2
    }
    this.setState({rectCoords: rectCoords});
  }



  render() {
    const lineConfig = this.props.lineConfig;
    const lineCoords = {
      x1: lineConfig.x1,
      x2: lineConfig.x2,
      y1: lineConfig.y1,
      y2: lineConfig.y2,
    };
    const textCoords = {
      x: (lineConfig.x1 + lineConfig.x2)/2,
      y: (lineConfig.y1 + lineConfig.y2)/2
    };
    return (
      <g>
        <line {...lineCoords} stroke="#000" markerEnd="url(#arrow)"></line>
        <rect {...this.state.rectCoords} fill="white"></rect>
        <text {...textCoords} ref="text" textAnchor="middle">{this.props.lineConfig.label}</text>
      </g>
    );
  }

}



