import React from 'react';
import 'core-js/fn/array/includes';

export default class GUMCIDInput extends React.Component {

  onKeyUp(e) {
    if (e.keyCode === 13){
      const xhr = new XMLHttpRequest();
      const gumcid = e.target.value;
      const json = JSON.stringify({
        gumcid: gumcid,
        arr: this.props.bpObjects.filter((bpObject) => !this.props.excludedTypes.includes(bpObject.type)).map((bpObject) => ({ id: bpObject.id, code: bpObject.code, type: bpObject.type}))
      });

      xhr.open('POST', this.props.loadUrl + '/json', true)
      xhr.setRequestHeader('Content-type', 'application/json');
      const input = this;
      xhr.onreadystatechange = function() {

        if (this.readyState !== 4) return;

        input.props.onResponse(xhr.responseText, gumcid);

      }

      xhr.send(json);


    }
  }
  
  render(){

    return (
      <div>
        <input className="gumcid-input" placeholder = "Введите номер сообщения (ГИС)" onKeyUp={this.onKeyUp.bind(this)} type="text"></input>
      </div>
    );
      
  }

}


