import React from 'react';
import ReactDOM from 'react-dom';

export default class BPObject extends React.Component {

  static get MINWIDTH() {
    return 100;
  }

  constructor(){
    super();
    this.onClick = this.onClick.bind(this);
  }

  componentDidMount() {
    const node = ReactDOM.findDOMNode(this);
    if (node.offsetWidth > BPObject.MINWIDTH || !this.props.excludedTypes.includes(this.props.type)){
      node.style.width =  BPObject.MINWIDTH*2 + 'px';
    }
    this.props.onMount(this,this.props.left+node.offsetWidth,this.props.top+node.offsetHeight);
  }

  onClick(){
    console.log({component_code: this.props.code, message_uuid: this.props.gumcid});
    if (window.parent && window.parent.formGenerator && typeof window.parent.formGenerator.createStandardGrid === 'function' && this.props.gumcid){
      window.parent.formGenerator.createStandardGrid({form_id: "2831d2d5-9d15-4eea-bd01-1b91afc81b7c"}, {component_code: this.props.code, message_uuid: this.props.gumcid})
    }
  }

  render() {
    const className = `bp-object ${this.props.type}${this.props.passStatus ? ' message' : ''}`;
    const id = 'bp-object-' + this.props.id;
    let footerClass = this.props.passStatus ? 'footer' : '';
    let footerText;
    if (!this.props.passStatus){
      footerText = '';
    }
    else {
      if (this.props.passStatus.result === 1){
        footerText = 'Узел пройден';
        footerClass+=' passed';
      }
      else if (this.props.passStatus.result === 0){
        footerText = 'Узел не пройден';
        footerClass+=' not-passed';
      }
      else if (this.props.passStatus.result === -1){
        footerText = this.props.passStatus.event_name + (this.props.passStatus.description ? (': ' + this.props.passStatus.description) : '');
        footerClass+=' error';
      }
    }
    return (
      <div className={className} id={id} style={{left: this.props.left, top: this.props.top}} onClick={this.onClick}>
        <div className="bp-object-name">
          <span className="name-text">{this.props.name}</span>
        </div>
        <div className={footerClass}>
          <span>{footerText}</span>
        </div>
      </div>
    );
  }

}


