import React from 'react';
import ReactDOM from 'react-dom';
import BPM from './BPM.js';
import config from './config/config.js';
import xml from './config/xml.js';
import './index.css'; 

function bpConfigParse(err, result){
  if(result.model && result.model.objects && result.model.objects[0] && result.model.objects[0].object && result.model.relations && result.model.relations[0] && result.model.relations[0].relation){
    const bpObjects = result.model.objects[0].object.map(function(bpObjectConfig){
        return {
          code: bpObjectConfig.code[0],
          id: +bpObjectConfig.id[0],
          left: +bpObjectConfig.location[0].split(',')[0],
          name: bpObjectConfig.name[0],
          top: +bpObjectConfig.location[0].split(',')[1],
          type: bpObjectConfig.type[0] 
        }
      }
    );
    const relations = result.model.relations[0].relation.map(function(relationConfig){
        return {
          end_object: +relationConfig.endobject[0],
          label: relationConfig.label[0],
          start_object: +relationConfig.startobject[0]
        }
      }
    );
    ReactDOM.render(
      <BPM bpObjects={bpObjects} relations={relations} config={config} title={result.model.$.title}/>,
      document.getElementById('root')
    );
  }
  else {
    alert('Incorrect xml config!');
  }
}

const xml2js = require('xml2js');
const parser = new xml2js.Parser();
parser.parseString(xml, bpConfigParse);

